import React from "react";
import { HOME } from "config";
import { Link } from "react-router-dom";

import './style.scss';
export default function ErrorPage(props) {
    return (
        <main style={{ minHeight: 'calc(100vh - 43px - 264px)' }}>
            <div className="container align--center error-page">
                <div className="gap--50"></div>
                <div className="gap--50"></div>
                <div className="gap--50"></div>
                <h1>404</h1>
                <h3>Page not found</h3>
                <div className="gap--50"></div>
                <div className="gap--50"></div>
                <Link className="btn black pi--50" to={HOME}>Back to home</Link>
            </div>
        </main>
    )
}